/* eslint no-undef: 0 */
/* eslint arrow-parens: 0 */
import React from 'react';
import { enquireScreen } from 'enquire-js';
import { graphql } from "gatsby";

import Layout from "../components/layout";
import News from '../components/antd/News';

import '../components/antd/less/antMotionStyle.less';

let isMobile;
enquireScreen((b) => {
  isMobile = b;
});

export default class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isMobile,
    };
  }

  componentDidMount() {
    // 适配手机屏幕;
    enquireScreen((b) => {
      this.setState({ isMobile: !!b });
    });
  }

  render() {
    const { data: { allNewsJson } } = this.props;
    return (
      <Layout>
        <News data={allNewsJson} />
      </Layout>
    );
  }
}

export const query = graphql`
    query {
        allNewsJson {
            nodes {
                id
                title
                image
                description
                link
            }
        }
    }
`

